export default {
	components: {},
	destroyed() {

	},
	data() {
		return {
			mawbfocus: true,
			mawb: '',

			warefocus: false,
			ware: '',

			bigfocus: false,
			big: '',

			ErrorRemind: '',
			InWareHouseNumber: 0,
			bigdisabled: "disabled"
		}
	},
	onLoad() {
		this.mawbfocus = true;
		this.mawb = '';

		this.warefocus = false;
		this.ware = '';

		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";

	},
	onShow() {
		this.mawbfocus = true;
		this.mawb = '';

		this.warefocus = false;
		this.ware = '';

		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";
	},
	methods: {
		focus_mawb() {
			if (this.mawb.length > 0) {
				this.mawb = '';
			}
			console.log('focus');
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);

		},
		focus_ware() {
			if (this.ware.length > 0) {
				this.ware = '';
			}
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		focus_big() {
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		//校验主单是否输出
		mawb_confirm() {
			console.log('该主单：' + this.mawb);
			var mawb = this.mawb.trim();
			if (mawb.length < 1) {
				this.bigdisabled = "disabled";
				return;
			}

			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			//var Url = 'http://localhost:7011/api/mmawb/get?mawb='+mawb;
			var Url = this.$websiteUrl + '/api/mmawb/get?mawb=' + mawb + '&user=' + user + '&token=' + token;
			uni.request({
				url: Url,
				method: 'GET',
				//data: {username:User_Value,password:Pass_Value},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));

					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}

					if (res.data.code == '200') {
						// if(res.data.result == null)
						// {
						// 	res.data.result = 2000;
						// }
						this.mawbfocus = false;
						this.warefocus = true;
						this.bigfocus = false;

						this.ErrorRemind = '';
						this.InWareHouseNumber = res.data.result == null ? 0 : res.data.result;

						if (this.mawb.length > 0 && this.ware.length > 0) {
							this.bigdisabled = false;
						}
					} else {
						this.ErrorRemind = res.data.text;
						this.mawb = '';
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		//验证入库单
		ware_confirm() {
			var ware = this.ware.trim();
			console.log('该库位：' + this.ware);
			if (ware.length < 1) {
				this.bigdisabled = "disabled";
				return;
			}
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			//var Url = 'http://localhost:7011/api/mstoragelocation/get?storagelocation='+ware;
			var Url = this.$websiteUrl + '/api/mstoragelocation/get?storagelocation=' + ware + '&user=' + user + '&token=' +
				token;
			console.log(Url);
			uni.request({
				url: Url,
				method: 'GET',
				//data: {username:User_Value,password:Pass_Value},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						this.mawbfocus = false;
						this.warefocus = false;
						this.bigfocus = true;
						this.ErrorRemind = '';
						//if(this.mawb.length > 0 && this.ware.length > 0)
						//{
						//	this.bigdisabled = false;
						//}
					} else {
						this.ErrorRemind = res.data.text;
						this.ware = '';
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		big_confirm() {
			var big = this.big.trim();
			var mawb = this.mawb.trim();
			var ware = this.ware.trim();
			console.log(big);
			if (mawb.length < 1 || ware.length < 1) {
				return;
			}

			//var Url = 'http://localhost:7011/api/minbound/post';
			var Url = this.$websiteUrl + '/api/minbound/post';

			var userName = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			uni.request({
				url: Url,
				method: 'POST',
				data: {
					mawb: mawb,
					storagelocation: ware,
					bagid: big,
					user: userName,
					token: token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						this.mawbfocus = false;
						this.warefocus = false;
						this.bigfocus = true;
						this.ErrorRemind = '';
						this.InWareHouseNumber = res.data.result;

						if (this.mawb.length > 0 && this.ware.length > 0) {
							this.bigdisabled = false;
						}
					} else {
						this.ErrorRemind = res.data.text;
					}

					this.big = '';
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		mawb_blur() {
			if (this.mawb.length > 0 && this.ware.length > 0) {
				this.bigdisabled = false;
			} else {
				this.bigdisabled = 'disabled';
			}
		},
		ware_blur() {
			if (this.mawb.length > 0 && this.ware.length > 0) {
				this.bigdisabled = false;
			} else {
				this.bigdisabled = 'disabled';
			}
		}
	}
}
